export default {
	data() {
		return {
			login_info: '',
			login_url: ''
		};
	},
	onLoad() {},
	methods: {
		onShow() {
			var me = this;
			// 使用挂载方法获取用户数据
			var userInfo = me.getGlobalUser('globalUser');
			if (userInfo != null) {
				var username = userInfo['username'];
				console.log(userInfo['id'])
				me.login_info = username;
				me.login_url = 'user';
			} else {
				me.login_info = '点击登录';
				me.login_url = 'login';
			}
		},
		denglu() {
			if(this.login_url == 'user'){
				// console.log(this.login_url);
				// uni.navigateTo({
				// 	url: 'user'
				// });
			}else if(this.login_url == 'login'){
				console.log(this.login_url);
				uni.navigateTo({
					url: '../login/login'
				});
			}
		},
		jifen() {
			//积分事件
			uni.showModal({
